<template>
  <div>
    <PageQatarPreHeader/>
    <div class="gamePlan pb-6">
      <v-container>
        <h3 class="titles">조별리그</h3>
        <v-card>
          <v-tabs
              v-model="tab"
              centered
              center-active
              dark
              show-arrows
              background-color="#024e94"
          >
            <v-tab
                v-for="i in groups"
                :key="i"
                :href="i.link">
              {{ i.name }}조
            </v-tab>
          </v-tabs>

          <v-tabs-items v-model="tab" touchless>
            <v-tab-item
                v-for="i in 8"
                :key="i"
                :value="'tab-' + i"
            >
              <v-card flat>
                <template>
                  <v-simple-table class="text-center px-2 px-sm-10">
                    <template v-slot:default>
                      <thead>
                      <tr>
                        <th
                            v-for="index in headers"
                            :key="index"
                            class="text-center"
                        >
                          {{ index }}
                        </th>
                      </tr>
                      </thead>
                      <tbody>
                      <tr
                          v-for="item in groupGames.filter((v) => v.Group == groups[i-1].name)"
                          :key="item"
                          @click="selectGame(item.HomeTeam, item.AwayTeam)"
                      >
                        <td>{{ item.HomeTeam }}</td>
                        <td>{{ item.AwayTeam }}</td>
                        <td>{{ item.Date }}</td>
                        <td>{{ item.Location }}</td>
                      </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </template>
              </v-card>
            </v-tab-item>
          </v-tabs-items>
        </v-card>
      </v-container>
    </div>
    <div class="tournamentBracket">
      <v-container>
        <h3 class="titles">결선 토너먼트</h3>
        <div class="d-none d-sm-flex justify-center text-center">
          <bracket :rounds="rounds">
            <template #player="{ player }">
              <div>{{ player.name }}</div>
            </template>
            <template #player-extension-bottom="{ match }">
              <div @click="selectGame(match.player1.name, match.player2.name)">
                {{ match.round }}<br>{{ match.date }}
              </div>
            </template>
          </bracket>
        </div>
        <div class="d-sm-none tournamentTable">
          <div v-for="r in 4">
            <h3>{{ rounds[r-1].roundTitle }}</h3>
            <template>
              <v-simple-table dense>
                <template v-slot:default>
                  <thead>
                  <tr>
                    <th class="text-left">
                      경기
                    </th>
                    <th class="text-left">
                      A팀
                    </th>
                    <th class="text-left">
                      B팀
                    </th>
                    <th class="text-left">
                      날짜
                    </th>
                  </tr>
                  </thead>
                  <tbody>
                  <tr
                      v-for="(item, index) in rounds[r-1].games"
                      :key="index"
                      @click="selectGame(item.player1.name, item.player2.name)"
                  >
                    <td>{{ item.round.substr(3) }}</td>
                    <td>{{ item.player1.name }}</td>
                    <td>{{ item.player2.name }}</td>
                    <td>{{ item.date }}</td>
                  </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </template>
          </div>
        </div>
      </v-container>
    </div>
    <div id="vsPrediction">
      <v-expand-transition>
        <v-container v-show="expand">
          <div class="vsPreContent">
            <h3 class="titles">전력비교</h3>
            <div v-if="countryAId">
              <template>
                <v-simple-table dense>
                  <template v-slot:default>
                    <thead>
                      <tr>
                        <th class="text-right" style="width:40%">{{ countryAName }}</th>
                        <th class="text-center" style="width:20%">vs</th>
                        <th class="text-left" style="width:40%">{{ countryBName }}</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td class="text-right">{{ countryA[0].Continent }}</td>
                        <td class="text-center">대륙</td>
                        <td class="text-left">{{ countryB[0].Continent }}</td>
                      </tr>
                      <tr>
                        <td class="text-right">{{ countryA[0].Manager }}</td>
                        <td class="text-center">감독</td>
                        <td class="text-left">{{ countryB[0].Manager }}</td>
                      </tr>
                      <tr>
                        <td class="text-right">{{ countryA[0].group }}</td>
                        <td class="text-center">조</td>
                        <td class="text-left">{{ countryB[0].group }}</td>
                      </tr>
                      <tr>
                        <td class="text-right">{{ countryA[0].Rank }}</td>
                        <td class="text-center">피파랭킹</td>
                        <td class="text-left">{{ countryB[0].Rank }}</td>
                      </tr>
                      <tr>
                        <td class="text-right">{{ countryA[0].Win }}</td>
                        <td class="text-center">승</td>
                        <td class="text-left">{{ countryB[0].Win }}</td>
                      </tr>
                      <tr>
                        <td class="text-right">{{ countryA[0].avg_goal }}</td>
                        <td class="text-center">평균득점</td>
                        <td class="text-left">{{ countryB[0].avg_goal }}</td>
                      </tr>
                      <tr>
                        <td class="text-right">{{ countryA[0].Avg_agn_goal }}</td>
                        <td class="text-center">평균실점</td>
                        <td class="text-left">{{ countryB[0].Avg_agn_goal }}</td>
                      </tr>
                      <tr>
                        <td class="text-right">{{ countryA[0].XG }}</td>
                        <td class="text-center">XG</td>
                        <td class="text-left">{{ countryB[0].XG }}</td>
                      </tr>
                      <tr>
                        <td class="text-right">{{ countryA[0].XGA }}</td>
                        <td class="text-center">XGA</td>
                        <td class="text-left">{{ countryB[0].XGA }}</td>
                      </tr>
                      <tr>
                        <td class="text-right">{{ countryA[0].Clean_seat }}</td>
                        <td class="text-center">클린시트</td>
                        <td class="text-left">{{ countryB[0].Clean_seat }}</td>
                      </tr>
                      <tr>
                        <td class="text-right">{{ (predictions[countryAId-1][countryBId+1] * 100).toFixed(3) + '%' }}</td>
                        <td class="text-center">승리예측</td>
                        <td class="text-left">{{ (predictions[countryBId-1][countryAId+1] * 100).toFixed(3) + '%' }}</td>
                      </tr>
                      <tr>
                        <td class="text-right">
                          <span
                              v-if="predictions[countryBId-1][countryAId+1] < predictions[countryAId-1][countryBId+1]"
                              class="d-inline-block"
                          >
                            <v-img
                                alt="logo"
                                class="shrink mr-2"
                                contain
                                src="@/assets/octopus.png"
                                transition="scale-transition"
                                width="40"
                            />
                          </span>
                        </td>
                        <td></td>
                        <td class="text-left">
                          <span
                              v-if="predictions[countryBId-1][countryAId+1] > predictions[countryAId-1][countryBId+1]"
                              class="d-inline-block"
                          >
                            <v-img
                                alt="logo"
                                class="shrink mr-2"
                                contain
                                src="@/assets/octopus.png"
                                transition="scale-transition"
                                width="40"
                            />
                          </span>
                        </td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </template>
            </div>
          </div>
        </v-container>
      </v-expand-transition>
    </div>
    <div class="firstPredict">
      <v-container>
        <h3 class="titles">Paul's Prediction</h3>
        <div class="chartContainer">
          <canvas id="chart"></canvas>
        </div>
      </v-container>
    </div>
  </div>
</template>

<script>
import PageQatarPreHeader from "@/components/layout/PageQatarPreHeader";
import http from "@/http-common";

import Bracket from "vue-tournament-bracket";

import { Chart, BarElement, BarController, LinearScale, CategoryScale } from 'chart.js';
Chart.register(BarElement, BarController, CategoryScale, LinearScale);
Chart.defaults.datasets.bar.barThickness = 16;
Chart.defaults.color = '#FFFFFF';

const rounds = [
  // 16G
  {
    roundTitle: "16강",
    games: [
      {
        player1: {id: "1", name: "Netherlands", winner: true},
        player2: {id: "2", name: "USA", winner: false},
        round: "16강 A", date: "12월 4일 00:00"
      },
      {
        player1: {id: "3", name: "Argentina", winner: true},
        player2: {id: "4", name: "Australia", winner: false},
        round: "16강 B", date: "12월 4일 04:00"
      },
      {
        player1: {id: "5", name: "Japan", winner: false},
        player2: {id: "6", name: "Croatia", winner: true},
        round: "16강 C", date: "12월 5일 00:00"
      },
      {
        player1: {id: "7", name: "Brazil", winner: true},
        player2: {id: "8", name: "Korea Republic", winner: false},
        round: "16강 D", date: "12월 5일 04:00"
      },
      {
        player1: {id: "9", name: "England", winner: true},
        player2: {id: "10", name: "Senegal", winner: false},
        round: "16강 E", date: "12월 6일 00:00"
      },
      {
        player1: {id: "11", name: "France", winner: true},
        player2: {id: "12", name: "Poland", winner: false},
        round: "16강 F", date: "12월 6일 04:00"
      },
      {
        player1: {id: "13", name: "Morocco", winner: true},
        player2: {id: "14", name: "Spain", winner: false},
        round: "16강 G", date: "12월 7일 00:00"
      },
      {
        player1: {id: "15", name: "Portugal", winner: true},
        player2: {id: "16", name: "Switzerland", winner: false},
        round: "16강 H", date: "12월 7일 04:00"
      }
    ]
  },
  // 8G
  {
    roundTitle: "8강",
    games: [
      {
        player1: {id: "1", name: "Netherlands"},
        player2: {id: "3", name: "Argentina"},
        round: "8강 A", date: "12월 10일 00:00"
      },
      {
        player1: {id: "6", name: "Croatia"},
        player2: {id: "7", name: "Brazil"},
        round: "8강 B", date: "12월 10일 04:00"
      },
      {
        player1: {id: "9", name: "England"},
        player2: {id: "11", name: "France"},
        round: "8강 C", date: "12월 11일 00:00"
      },
      {
        player1: {id: "13", name: "Morocco"},
        player2: {id: "15", name: "Portugal"},
        round: "8강 D", date: "12월 11일 04:00"
      }
    ]
  },
  // 4G
  {
    roundTitle: "4강",
    games: [
      {
        player1: { id: "25", name: "8강 A 승자"},
        player2: { id: "26", name: "8강 B 승자"},
        round: "4강 A", date: "12월 14일 04:00"
      },
      {
        player1: { id: "27", name: "8강 C 승자"},
        player2: { id: "28", name: "8강 D 승자"},
        round: "4강 B", date: "12월 14일 04:00"
      }
    ]
  },

  // final
  {
    roundTitle: "결승",
    games: [
      {
        player1: { id: "29", name: "4강 A 승자"},
        player2: { id: "30", name: "4강 B 승자"},
        round: "결승 A", date: "12월 19일 00:00"
      }
    ]
  }
];

export default {
  name: 'PageQatarPre',

  components: {
    PageQatarPreHeader,
    Bracket
  },

  data () {
    return {
      i: 0,
      tab: null,
      expand: false,
      rounds: rounds,
      countries: [],
      groupGames: [],
      predictions: [],
      firstPrediction: [],
      firstPredictionName: [],
      firstPredictionVal: [],
      firstPredictChart: null,
      countryA: "", countryB: "",
      countryAId: 0, countryBId: 0,
      countryAName: "", countryBName: "",
      headers: [ "Home", "Away", "Date", "Location" ],
      groups: [
        {link: "#tab-1", name: "A"}, {link: "#tab-2", name: "B"},
        {link: "#tab-3", name: "C"}, {link: "#tab-4", name: "D"},
        {link: "#tab-5", name: "E"}, {link: "#tab-6", name: "F"},
        {link: "#tab-7", name: "G"}, {link: "#tab-8", name: "H"},
      ],
    }
  },

  methods:{
    dataSchedule(){
      http
          .get("/qatar/qpre/game")
          .then(response=>{
            this.groupGames = response.data;
            console.log(response.data);
          })
          .catch(e=>{
            console.log(e);
          })
    },
    dataPrediction() {
      return http
          .get("/qatar/qpre/prediction")
          .then(response=>{
            response.data.forEach((item)=>{
              this.predictions.push([
                item.country_number, item.country_name, item.vs_Qatar, item.vs_Ecuador, item.vs_Senegal,
                item.vs_Netherlands, item.vs_England, item.vs_Iran, item.vs_USA, item.vs_Wales,
                item.vs_Argentina, item.vs_SaudiArabia, item.vs_Mexico, item.vs_Poland, item.vs_France,
                item.vs_Australia, item.vs_Denmark, item.vs_Tunisia, item.vs_Spain, item.vs_CostaRica,
                item.vs_Germany, item.vs_Japan, item.vs_Belgium, item.vs_Canada, item.vs_Morocco,
                item.vs_Croatia, item.vs_Brazil, item.vs_Serbia, item.vs_Switzerland, item.vs_Cameroon,
                item.vs_Portugal, item.vs_Ghana, item.vs_Uruguay, item.vs_KoreaRepublic, item.first_in_group,
                item.second_in_group, item.go_to_r16, item.go_to_r8_in_left, item.go_to_r8_in_right, item.go_to_r8,
                item.fail_in_r16_left, item.fail_in_r16_right, item.fail_in_r16, item.go_to_r4_in_left, item.go_to_r4_in_right,
                item.go_to_r4, item.fail_in_r8_left, item.fail_in_r8_right, item.fail_in_r8, item.go_to_final_in_left,
                item.go_to_final_in_right, item.go_to_final, item.go_to_rank_in_left, item.go_to_rank_in_right, item.go_to_rank,
                item.third_in_rank, item.fourth_in_rank, item.first_in_final, item.second_in_final
              ])
              this.firstPrediction.push({
                name: item.country_name, per: item.first_in_final
              })
            })

            this.firstPrediction.sort(function (a, b){
              return b.per - a.per;
            });
            this.firstPredictionName = this.firstPrediction.map(row => row.name);
            this.firstPredictionVal = this.firstPrediction.map(row => row.per);

            console.log(this.predictions);
          })
          .catch(e=>{
            console.log(e);
          })
    },
    dataCountry() {
      http
          .get("/qatar/qpre/country")
          .then(response=>{
            this.countries = response.data;
            console.log(response.data);
          })
          .catch(e=>{
            console.log(e);
          })
    },
    selectGame: function(home, away) {
      // 페이지 확장
      this.expand = true;
      // 국가 이름 저장
      this.countryAName = home;
      this.countryBName = away;
      // 국가 데이터 불러오기
      this.countryA = this.countries.filter((i) => i.Name == this.countryAName);
      this.countryB = this.countries.filter((i) => i.Name == this.countryBName);
      // 국가 번호 저장
      this.countryAId = this.countryA[0].CID;
      this.countryBId = this.countryB[0].CID;
      // 페이지 이동
      document.getElementById('vsPrediction').scrollIntoView();
    },
    fillChartData: function(){
      const ctx = document.getElementById('chart').getContext('2d');
      this.firstPredictChart = new Chart(ctx,{
        type: 'bar',
        data: {
          labels: this.firstPredictionName,
          datasets: [{
            axis: 'y',
            label: '확률',
            data: this.firstPredictionVal,
            backgroundColor: '#eeeee4'
          }],
        },
        options: {
          maintainAspectRatio: false,
          responsive: true,
          indexAxis: 'y',
        }
      })
    },
  },

  mounted(){
    this.dataSchedule();
    this.dataPrediction().then(() => {
      this.fillChartData();
    });
    this.dataCountry();
  },
}
</script>

<style lang="scss">
@import "@/assets/scss/_PageQatarPre.scss";
</style>